/* eslint react/no-did-mount-set-state:0 */
import React, { Component } from 'react';
import './App.css';
import { Poster } from './Movie';
import styled from 'styled-components';
import Overdrive from 'react-overdrive';

const POSTER_PATH = 'http://image.tmdb.org/t/p/w154';
const BACKDROP_PATH = 'http://image.tmdb.org/t/p/w1280';

class MovieDetail extends Component {
state = {
	movie: {},
}

async componentDidMount() {
	try {
		const result = await fetch(`https://api.themoviedb.org/3/movie/${this.props.match.params.id}?api_key=424486d357c48d17c5f6c5bee2950f9c&language=en-US`);
		const movie = await result.json();
		this.setState({ movie });
	} catch (e) {
		console.log(e);
	}
}
render() {
	const { movie } = this.state;

	return (
		<MovieWrapper backdrop={`${BACKDROP_PATH}${movie.poster_path}`} >
			<MovieInfo>
				<Overdrive id={movie.id}>
					<Poster src={`${POSTER_PATH}${movie.poster_path}`} alt="8=D" />
				</Overdrive>
				<div>
					<h1> {movie.title} </h1>
					<h3> {movie.release_date} </h3>
					<p> {movie.overview} </p>
				</div>
			</MovieInfo>
		</MovieWrapper>
	);
}
}

export default MovieDetail;

const MovieWrapper = styled.div`
    position: relative;
    padding-top: 50vh;
    background: url(${props => props.backdrop}) no-repeat;
    background-size: cover;

`;

const MovieInfo = styled.div`
    padding: 2rem 10%;
    text-align: left;
    display: flex;
    background: white;

    > div {
        margin-left: 20px;
    }
    >img {
        position: relative;
        top: -5rem;
    }
`;



